<template>
	<view class="body">
		<view class="title">
			<view class="number">{{data.index}}</view>
			<view class="text">{{data.title}}</view>
		</view>
		<view class="words">
			<view v-for="(item,index) in data.data" :key="index" class="express_content">
				<view>
					<view class="h5">{{item.word}}</view>
					<view class="h6">{{item.explain}}</view>
				</view>
				<u-icon v-if="item.is_collection==false" name="star-fill" color="#e3e3e3" size="48rpx" @click="isStar(item)"></u-icon>
				<u-icon v-else name="star-fill" color="#ffd75c" size="48rpx" @click="isStar(item)"></u-icon>
			</view>
		</view>
		<button @click="toNext">下一步，去看句子</button>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import { subject, reSubject, setCollection } from '@/common/http.api.js';
	export default {
		data() {
			return {
				data:{},
				orders:''
			}
		},
		onLoad(option) {
			let token = store.state.vuex_token 
			let id = store.state.subject_id 
			if(option.id){
				reSubject({token:token,subject_id:option.id}).then(res=>{
					console.log(res)
					subject({token:token,subject_id:option.id}).then(res=>{
						console.log(res)
						this.data = res.data.data
						this.orders = res.data.data.subject_order
						console.log("roaosfj"+this.orders)
					}).catch(err=>{
						console.log(err)
					})
				}).catch(err=>{
					console.log(err)
				})
			}else{
				subject({token:token,subject_id:id}).then(res=>{
					console.log(res)
					this.data = res.data.data
				}).catch(err=>{
					console.log(err)
				})
			}
			
		},
		onShow() {
			let token = store.state.vuex_token
			let id = store.state.subject_id 
			subject({token:token,subject_id:id}).then(res=>{
				console.log(res)
				this.data = res.data.data
				this.orders = res.data.data.subject_order
				console.log("roaosfj"+this.orders)
			}).catch(err=>{
				console.log(err)
			})
		},
		methods:{
			// 点击收藏
			isStar(item) {
				let token = store.state.vuex_token 
				let id = store.state.subject_id 
				console.log(item)
				setCollection({token, rare_word_id:item.id}).then(res=>{
					console.log(res)
					subject({token:token,subject_id:id}).then(res=>{
						console.log(res)
						this.data = res.data.data
					}).catch(err=>{
						console.log(err)
					})
				}).catch(err=>{
					console.log(err)
				})
			},
			// 下一步
			toNext() {
				this.$u.vuex('order', this.orders)
				uni.navigateTo({
					url: '/pages/index/learnOther'
				})
			}
		}
	}
</script>

<style lang="scss">
	.body{
		padding: 40rpx;
		padding-bottom: 128rpx;
		.title{
			display: flex;
			margin-bottom: 30rpx;
			.number{
				background-color: #797979;
				color: #fff;
				width: 36rpx;
				line-height: 36rpx;
				height: 36rpx;
				text-align: center;
				margin-right: 20rpx;
			}
			.text{
				line-height: 36rpx;
				font-size: 30rpx;
				font-weight: 500;
				letter-spacing: 2rpx;
			}
		}
		.express_content{
			display: flex;
			justify-content: space-between;
			margin: 20rpx 0;
		}
		.h5 {
			font-size: 32rpx;
			font-weight: 500;
			line-height: 48rpx;
		}
		.h6 {
			font-size: 24rpx;
			line-height: 36rpx;
			color: #999;
			font-weight: 400;
			margin-bottom: 20rpx;
		}
		button{
			background: linear-gradient(to right, #ffd23f, #fca51d);
			color: #fff;
			font-size: 32rpx;
			line-height: 88rpx;
			border-radius: 100rpx;
			width: 80%;
			margin: 0 10%;
		}
	}
</style>